<template>
  <div>
    <bread-nav :nav="nav"/>

    <div class="card"
         v-if="surveyInfo.id">
      <div class="content">
        <p class="title">{{surveyInfo.title}}</p>
        <div class="prompt">
          <span>填写人：{{surveyInfo.name || '--'}}</span>
          <span>子女：{{surveyInfo.childrenList.map(child => child.studentName).join(', ') || '--'}}</span>
          <span>手机号: {{surveyInfo.phone}}</span>
          <span>提交时间: {{surveyInfo.createdTime}}</span>
        </div>
        <p v-html="surveyInfo.content"></p>
      </div>

      <!--<h1 class="section__title">题目</h1>-->
      <div v-for="(question, questionIdx) in surveyInfo.questionList"
           :key="questionIdx"
           class="section__content bg-gray">
        <p class="question__title">{{questionIdx + 1}}、{{question.title}}</p>

        <div v-if="question.optionList">
          <div v-for="(option, optionIdx) in question.optionList"
               :key="optionIdx"
               class="section__question">
            <el-checkbox
                :checked="option.isSelected === 'YES'">
              {{String.fromCharCode(optionIdx + 65) + '、'}}
            </el-checkbox>
            <div class="layer"></div>
            <!--<span class="el-radio"></span>-->
            <!--<el-input v-model="question.optionList[optionIdx]"/>-->
            <span>{{option.optionTitle}}</span>
          </div>
        </div>

        <p v-if="question.questionType === 'COMPLETION'"
           class="mt15">{{question.answer || '--'}}</p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        nav: [
          {name: '调查问卷', path: '/student-work/survey'},
          {name: '查看', path: 'go(-1)'},
          {name: '详情', path: ''}
        ],
        id: this.$route.params.id,
        recordId: this.$route.params.recordId,
        surveyInfo: {}
      }
    },
    async mounted () {
      this.surveyInfo = await this.$ajax({
        url: '/survey/getSurveyRecord.do',
        query: {
          id: this.recordId
        }
      })
    }
  }
</script>

